import { Component, EventEmitter, OnDestroy, OnInit, Output } from '@angular/core';
import { MatDialogRef } from '@angular/material/dialog';
import { BarcodeFormat } from '@zxing/library';
import { ZXingScannerComponent } from '@zxing/ngx-scanner';

// Extender el tipo MediaTrackConstraintSet para incluir torch
interface MediaTrackConstraintSet {
  torch?: boolean; // Propiedad torch para controlar la linterna
}

@Component({
  selector: 'app-lector-qr',
  templateUrl: './lector-qr.component.html',
  styleUrls: ['./lector-qr.component.scss']
})
export class LectorQrComponent implements OnInit, OnDestroy {
  @Output() sendObject = new EventEmitter<any>();
  scannerEnabled: boolean = true; // Control para activar o detener el escner
  availableDevices: MediaDeviceInfo[] = [];
  selectedDevice: MediaDeviceInfo | null = null;
  hasDevices: boolean = false;
  hasPermission: boolean = false;
  currentDeviceIndex: number = 0; // ndice de la cmara actual
  torchEnabled: boolean = false; // Control para la linterna

  constructor(private dialogRef: MatDialogRef<LectorQrComponent>) { }

  ngOnDestroy(): void {
    this.stopScanner();
  }

  ngOnInit(): void {
    // Inicializar el escner si es necesario
  }

  onCodeResult(resultString: string) {
    this.sendObject.emit(resultString);
    this.stopScanner();
  }

  onCamerasFound(devices: MediaDeviceInfo[]): void {
    this.availableDevices = devices;
    this.hasDevices = Boolean(devices && devices.length);
    if (this.hasDevices) {
      // Seleccionar la primera cmara disponible
      this.selectedDevice = devices[0];
      this.currentDeviceIndex = 0; // Inicializar ndice de la cmara
    }
  }

  onHasPermission(has: boolean): void {
    this.hasPermission = has;
  }

  // Mtodo para detener el escner
  stopScanner(): void {
    if (this.scannerEnabled) { // Verifica si el escner est habilitado
      this.scannerEnabled = false; // Desactivar el escner

      console.log("Escner detenido."); // Mensaje de confirmacin

      // Manejo del backdrop overlay si se est ejecutando dentro de un modal
      const overlayBackdrop = document.querySelector('.cdk-overlay-backdrop');
      if (overlayBackdrop) {
        // Verifica si hay ms de un overlay
        const overlays = document.querySelectorAll('.cdk-overlay-backdrop');
        if (overlays.length > 1) {
          // Elimina el ltimo overlay
          overlays[overlays.length - 1].remove();
        } else {
          // Si solo hay un overlay, elimina ese
          overlayBackdrop.remove();
        }
      }
    }
    
    // Cerramos el dilogo
    this.dialogRef.close();
  }

  startScanner(): void {
    this.scannerEnabled = true;
  }

  // Mtodo para cambiar entre cmaras
  switchCamera(): void {
    if (this.hasDevices) {
      this.currentDeviceIndex = (this.currentDeviceIndex + 1) % this.availableDevices.length; // Cambiar al siguiente ndice
      this.selectedDevice = this.availableDevices[this.currentDeviceIndex]; // Actualizar la cmara seleccionada
      console.log(`Cmara cambiada a: ${this.selectedDevice.label}`); // Mensaje de confirmacin
      // Aqu puedes reiniciar el escner si es necesario
    }
  }

  // Mtodo para encender y apagar la linterna
  toggleTorch(): void {
    if (this.selectedDevice) {
      const constraints = {
        video: {
          deviceId: { exact: this.selectedDevice.deviceId },
          facingMode: 'environment' // Asegrate de usar la cmara trasera
        }
      };

      navigator.mediaDevices.getUserMedia(constraints)
        .then((stream) => {
          const videoTrack = stream.getVideoTracks()[0];
          if (videoTrack) {
            const capabilities = videoTrack.getCapabilities();
            if (capabilities['torch']) { // Verifica si el dispositivo tiene linterna
              this.torchEnabled = !this.torchEnabled; // Cambia el estado de la linterna
              // Aplicar las restricciones avanzadas
              let advanced = 'advanced';
              videoTrack.applyConstraints({ [advanced]: [{ torch: this.torchEnabled }] as MediaTrackConstraintSet })
                .then(() => {
                  console.log(`Linterna ${this.torchEnabled ? 'encendida' : 'apagada'}.`);
                })
                .catch(err => {
                  console.error('Error al cambiar el estado de la linterna:', err);
                });
            }
          }
        })
        .catch(err => {
          console.error('Error al acceder a la cmara:', err);
        });
    }
  }
}